import styled from "@emotion/styled";
import Banner1 from "../images/banner/01.jpg";
import Banner2 from "../images/banner/02.jpg";
import Banner3 from "../images/banner/03.jpg";
import "../basic.css";

const BannerTitle = styled.p`
    font-size: 26px;
    font-weight: 700;
    color: #323232;
    margin-bottom: 8px;
`;

const BannerInfo = styled.p`
    font-size: 18px;
    font-weight: 350;
    color: #323232;
`;

const BannerUl = styled.ul`
    width: 100%;
`;

const BannerLi = styled.li`
    position: relative;
    height: 343px;
    overflow: hidden;

    `;

const BannerImg = styled.div`
    width: 100%;
    height: 343px;

    &:hover {
        transform: scale(1.1);
    }
    `;

const BannerImgImg = styled.img`
    height: 343px;
    transition: transform 800ms;
`;

const BannerText = styled.div`
    position: absolute;
    left: 40px;
    bottom: 38px;
`;

export const BannerCons = () => {


    return(
        <BannerUl className="row">
            <BannerLi>
                <BannerImg>
                    <BannerImgImg src={Banner1} alt="" />
                </BannerImg>
                <BannerText>
                    <BannerTitle>
                      ! <br />
                    VIP  
                    </BannerTitle>
                    <BannerInfo>
                     vs  <br />
                       
                    </BannerInfo>
                </BannerText>
            </BannerLi>
            <BannerLi>
                <BannerImg>
                    <BannerImgImg src={Banner2} alt="" />
                </BannerImg>
                <BannerText>
                    <BannerTitle>
                       <br />
                      
                    </BannerTitle>
                    <BannerInfo>
                       <br />
                    #1:1    
                    </BannerInfo>
                </BannerText>
            </BannerLi>
            <BannerLi>
                <BannerImg>
                    <BannerImgImg src={Banner3} alt="" />
                </BannerImg>
                <BannerText>
                    <BannerTitle>
                        <br />
                    1+1, SALE!
                    </BannerTitle>
                    <BannerInfo>
                       <br />
                    2  
                    </BannerInfo>
                </BannerText>
            </BannerLi>
        </BannerUl>

    );
};