import JoinHeader from "./joinHeader"
import * as React from 'react';
import Checkbox from '@mui/material/Checkbox';
import { HeaderCons } from "./header";
import { useState } from "react";
import { useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { AgreementContainer, Btn, ButtonContainer, CheckBoxContainer, CheckedAll, CheckedAllBox, CheckedBox1, CheckedBox2, Checkedleft, Checkedright, ContentBox, Lable, OptionBox, Process, ProcessDetailArrow, View } from "../styled/agreementStyled";
import { FlexContainer, ProcessDetail } from "./joinsuccess";

const label = { inputProps: { 'aria-label': 'Checkbox demo' } };


export const AgreementPage = ()=>{
    // 
    const [allCheck, setAllCheck] = useState(false);
    // ()
    const [ageCheck, setAgeCheck] = useState(false);
    // ()
    const [useCheck, setUseCheck] = useState(false);
    // ()
    const [shoppingCheck, setShoppingCheck] = useState(false);
    //sms ()
    const [smsCheck, setSmsCheck] = useState(false);
    //()
    const [emailCheck, setEmailCheck] = useState(false);

    //  onChange 
    const allBtnEvent =()=>{
        if(allCheck === false) {
          setAllCheck(true);
          setAgeCheck(true);
          setUseCheck(true);
          setShoppingCheck(true);
          setSmsCheck(true);
          setEmailCheck(true);
        }else {
          setAllCheck(false);
          setAgeCheck(false);
          setUseCheck(false);
          setShoppingCheck(false);
          setSmsCheck(false);
          setEmailCheck(false);
        } 
      };

      const ageBtnEvent =()=>{
        if(ageCheck === false) {
          setAgeCheck(true)
        }else {
          setAgeCheck(false)
        }
      };

      const useBtnEvent =()=>{
        if(useCheck === false) {
          setUseCheck(true)
        }else {
          setUseCheck(false)
        }
      };

      const shoppingCheckBtnEvent =()=>{
        if(shoppingCheck === false) {
            setSmsCheck(true);
            setEmailCheck(true);
          }else {
            setSmsCheck(false);
            setEmailCheck(false);
          } 
        };

      const SmsCheckBtnEvent =()=>{
        if(smsCheck === false) {
            setSmsCheck(true)
        }else {
            setSmsCheck(false)
        }
      };
      const emailCheckBtnEvent =()=>{
        if(emailCheck === false) {
            setEmailCheck(true)
        }else {
            setEmailCheck(false)
        }
      };

 
      useEffect(()=>{
        if(ageCheck===true && useCheck===true && shoppingCheck===true && smsCheck===true && emailCheck===true){
          setAllCheck(true)
        } else {
          setAllCheck(false)
        }
      }, [ageCheck,useCheck,shoppingCheck,smsCheck,emailCheck])

      useEffect(()=>{
        if(ageCheck===true && useCheck===true){
            setAllCheck(true)
        }else{
            setAllCheck(false)
        }
      },[ageCheck, useCheck])

      useEffect(()=>{
        if(smsCheck===true && emailCheck===true){
            setShoppingCheck(true)
        }else{
            setShoppingCheck(false)
        }
      },[smsCheck,emailCheck])
    const navigate = useNavigate();

    const submit = ()=>{
      if(allCheck===true || (useCheck===true && ageCheck===true) ){
          navigate('/join_membership')
      }else if(!useCheck===true || (shoppingCheck===true && smsCheck===true && emailCheck===true)){
         alert('')
      }else if(!allCheck===true || !(useCheck===true && ageCheck===true)){
        navigate('/join_membership')
      }
    }
      const Main = ()=>{
        navigate('/')
      }
    return(
            <>
            <HeaderCons />
            <JoinHeader/>
            <Process>
                    <FlexContainer>
                        <ProcessDetailArrow style={{color:'black'}}>1. </ProcessDetailArrow>
                        <ProcessDetailArrow>2. </ProcessDetailArrow>
                        <ProcessDetail>3. </ProcessDetail>
                    </FlexContainer>
                </Process>
            {/* form */}
            <AgreementContainer onSubmit={submit}>
                <CheckedAll></CheckedAll>
                <CheckBoxContainer>
                    <Checkbox {...label} id="all-check" checked={allCheck} onChange={allBtnEvent} />
                    <Lable>    ,  ()  . </Lable>
                </CheckBoxContainer>
                <CheckedAllBox>
                    <Checkedleft>
                        <CheckBoxContainer>
                            <Checkbox {...label} id="check1" checked={ageCheck} onChange={ageBtnEvent}/>
                            <Lable>  ()</Lable>
                        </CheckBoxContainer>
                        <ContentBox>
                            <View>

                            <p>1()</p>
                            <br />
                               ( )    
                            ( ""  )    ( ""  ) 
                                 ,      .
                            <br />
                            PC,             .
                            <br/>
                            <br/>
                            <br/>
                            <p>2()</p>
                                   (   )      
                                      ,    
                              .
                            <br/>
                                          .
                            <br />
                            <p>3 (     )</p>
                                    ,   (       ), 
                            .., ,  ,       00 
                              () . ,           .
                            </View>
                        </ContentBox>
                    </Checkedleft>
                    <Checkedright>
                    <CheckBoxContainer>
                            <Checkbox {...label} id="check2" checked={useCheck}  onChange={useBtnEvent} />
                            <Lable> ()</Lable>
                        </CheckBoxContainer>
                        <CheckedBox1>
                            <View>
                            **              . 
                              ,          . **
                            <br/>
                            <br/>
                            <p>1.    </p>
                            <br/>
                            .          
                            <br/>
                             ,    ,     ,      
                            <br/>
                            <br/>
                            .  
                            <br/>
                                 ,   ,        ,    , 
                             , 14         ,    ,  

                            </View>
                        </CheckedBox1>
                        <br/>
                        <CheckBoxContainer>
                            <Checkbox {...label} id="check3" checked={shoppingCheck}  onChange={shoppingCheckBtnEvent}/>
                            <Lable>   ()</Lable>
                        </CheckBoxContainer>
                        <CheckBoxContainer>
                            <OptionBox>
                                <Checkbox {...label} id="check4" checked={smsCheck}  onChange={SmsCheckBtnEvent}/>
                                <Lable style={{fontSize:"14px"}}>SMS  ()</Lable>
                                <Checkbox {...label} id="check5" checked={emailCheck}  onChange={emailCheckBtnEvent}/>
                                <Lable style={{fontSize:"14px"}}>   ()</Lable>
                            </OptionBox>
                        </CheckBoxContainer>
                        <CheckedBox2>
                            <p>  , ,        SMS    . 
                                , /          .</p>
                            <br/>
                            <br/>
                            <p>     ,         .</p>
                        </CheckedBox2>
                    </Checkedright>
                </CheckedAllBox>
                <ButtonContainer>
                    <Btn style={{marginRight:"15px"}} onClick={Main}></Btn>
                    <Btn style={{backgroundColor:"black", color:"white"}} type="submit"><Link to={'/join_membership'}></Link></Btn>
                </ButtonContainer>
            </AgreementContainer>
        </>
    )
};
