
import React from 'react';
import {useFormik} from "formik";
import {object,ref,string} from "yup"
import { useState } from 'react';
import PopupPostCode from './PopupPostCode';
import { ErrMsg, AddText, AddressArea, AddressNumber, BasicInformation, BirthSolar, CheckedItem, InputAddress, InputBirth, InputInformation, InputPhone, Item, JoinBtnContainer, SearchAddressBtn, SelectContainer, TextInput, Title, Tr, InputAddress2, Process, FlexContainer, ProcessDetailArrow, ProcessDetail, InputBasicPhone, ErrMsg2 } from './styled/joinMembershipStyled';
import { useNavigate } from 'react-router-dom';
import { JoinHeader } from './components/joinHeader';
import { HeaderCons } from './components/header';
// import * as React from 'react';
import Radio from '@mui/material/Radio';


  


const username = /^[-]+$/;
// const phoneCheck = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{4,4}$/;
const idcheck1 = /^(?=.*[a-z])(?=.*[0-9]).{4,10}$/;
const idcheck2 = /^(?=.*[a-z])(?=.*[^a-z0-9]).{4,10}$/;
const idcheck3 = /^(?=.*[^a-z0-9])(?=.*[0-9]).{4,10}$/;
const passwordcheck = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,15}$/;


let userSchema = object({
    id:string(' ')
        .matches(idcheck1 || idcheck2 || idcheck3, {message:'  '})
        .required(''),
    password:string(' ')
        .required('')
        .matches(passwordcheck,'  '),
    passwordCheck:string(' ')
        .oneOf([ref('password'), null], '  ')
        .min(6, '6  ')
        .max(20, '20  ')
        .required(''),
    name:string(' ')
        .required('')
        .matches(username,' '),
    phone:string(' ')
        .min(11, '11 ')
        .max(11)
        .matches(/^[0-9]+$/, ' .')
        // .test()
        .required(''),
    phone2:string(' ')
        .min(11, '11 ')
        .max(11)
        .matches(/^[0-9]+$/, ' .'),
    email:string(' ')
        .email('  ')
        .max(30, '30  ')
        .required('.'),
    zipCode : string(' ').required(''),
    address : string(' ').required(''),
    addressDetail : string(' ').notRequired(),
    birth:string(' ')
        .min(4, '4 ')
        .max(4)
        .matches(/^[0-9]+$/, ' .'),
    month:string(' ')
        .min(2, '2 ')
        .max(2)
        .matches(/^[0-9]+$/, ' .'),
    date:string(' ')
        .min(2, '2 ')
        .max(2)
        .matches(/^[0-9]+$/, ' .'),
});



export const JoinMembershipPage=()=>{
    const [selectedValue, setSelectedValue] = React.useState('a');
  
    const handleChange = (event) => {
      setSelectedValue(event.target.value);
    }


    const navigate = useNavigate();
    //  
    const [isPopupOpen, setIsPopupOpen] = useState(false);

    // 
    const openPostCode = ()=>{
        setIsPopupOpen(true);
        
    }

    // 
    const closePostCode = ()=>{
        setIsPopupOpen(false)
        
    }
    const formik = useFormik({
        initialValues : {
            email:'', 
            password:'', 
            passwordCheck:'',
            name:'',
            phone:'',
            id:'',
            zipCode:'',
            address:'',
            addressDetail:'',
            birth:'',
            month:'',
            date:''
        },
        onSubmit : ()=>{
           navigate('/join_success');
        },
        validationSchema : userSchema
    });

    const handlePostCode = (data)=>{
        let fullAddress = data.address;
        let extraAddress = '';

        if(data.addressType === 'R'){
            if(data.bname !== ''){
                extraAddress += data.bname;
            }
            if(data.buildingName !== ''){
                extraAddress += (extraAddress !== '' ? 
                `, ${data.buildingName}` : data.buildingName);
            }
            fullAddress += (extraAddress !== '' ? ` (${extraAddress})` : '' );
        }
        console.log(data)
        console.log(fullAddress)
        console.log(data.zonecode)

        formik.setFieldValue('address', fullAddress);
        formik.setFieldValue('zipCode', data.zonecode);
        setIsPopupOpen(false);
    }
    
   
    return(
    <>
            <HeaderCons/>
            <JoinHeader/>
            <Process>
                    <FlexContainer>
                        <ProcessDetailArrow>1. </ProcessDetailArrow>
                        <ProcessDetailArrow style={{color:'black'}}>2. </ProcessDetailArrow>
                        <ProcessDetail>3. </ProcessDetail>
                    </FlexContainer>
                </Process>
                <form 
                    style={{maxWidth:'898px', margin:'0 auto'}}
                    onSubmit={formik.handleSubmit}>
                    <Title>
                        <h3></h3>
                        <p></p>
                    </Title>
                    <BasicInformation>
                        <table>
                            <tbody>
                                <Tr>
                                    <CheckedItem></CheckedItem>
                                    <td>
                                        <InputInformation
                                            onBlur={formik.handleBlur}
                                            name="id"
                                            onChange={formik.handleChange}
                                            value={formik.values.id}
                                            placeholder="">
                        
                                        </InputInformation>
                                             {
                                            formik.touched.id && formik.errors.id &&
                                            <ErrMsg>{formik.errors.id}</ErrMsg>
                                             }
                                        <AddText>(   4~10)</AddText>
                                        
                                    </td>
                                    
                                </Tr>
                                <Tr>
                                    <CheckedItem></CheckedItem>
                                    <td>
                                        <InputInformation 
                                            onBlur={formik.handleBlur}
                                            name='password'
                                            type='password'
                                            onChange={formik.handleChange}
                                            value={formik.values.password}
                                            placeholder=''>
                                        </InputInformation>
                                        {
                                            formik.touched.password && formik.errors.password &&
                                            <ErrMsg>{formik.errors.password}</ErrMsg>
                                        }
                                        <AddText>(//  8~15)</AddText>
                                    </td>
                                </Tr>
                                <Tr>
                                    <CheckedItem></CheckedItem>
                                    <td>
                                        <InputInformation 
                                            onBlur={formik.handleBlur}
                                            name='passwordCheck'
                                            type='password'
                                            onChange={formik.handleChange}
                                            value={formik.values.passwordCheck}
                                            placeholder=''>
                                        </InputInformation>
                                        {
                                            formik.touched.passwordCheck && formik.errors.passwordCheck &&
                                            <ErrMsg>{formik.errors.passwordCheck}</ErrMsg>
                                        }
                                    </td>
                                </Tr>
                                <Tr>
                                    <CheckedItem></CheckedItem>
                                    <td>
                                        <InputInformation 
                                            onBlur={formik.handleBlur}
                                            name='name'
                                            onChange={formik.handleChange}
                                            value={formik.values.name}
                                            placeholder=''>
                                        </InputInformation>
                                            {
                                                formik.touched.name && formik.errors.name &&
                                                <ErrMsg>{formik.errors.name}</ErrMsg>
                                                }
                                    </td>
                                </Tr>
                                <Tr>
                                    <CheckedItem></CheckedItem>
                                    <AddressArea>
                                        <ul>
                                            <AddressNumber>
                                                <InputAddress
                                                 name="zipCode" 
                                                 placeholder=""
                                                 value={formik.values.zipCode}
                                                 onChange={formik.handleChange}
                                                 onBlur={formik.handleBlur}
                                                 readOnly>
                                                </InputAddress>
                                                <SearchAddressBtn type="button" 
                                                 onClick={openPostCode}>
                                                </SearchAddressBtn>
                                                {
                                                    isPopupOpen && <PopupPostCode
                                                     handlePostCode={ handlePostCode}
                                                    onClose = {closePostCode}/> 
                                                }
                                                 {
                                                 formik.touched.zipCode && formik.errors.zipCode &&
                                                 <li><ErrMsg>{formik.errors.zipCode}</ErrMsg></li>
                                                }
                                            </AddressNumber>
                                            <AddressNumber>
                                            <InputAddress2 
                                                name="address" 
                                                placeholder=""
                                                value={formik.values.address}
                                                onChange={formik.handleChange}
                                                onBlur={formik.handleBlur}
                                                readOnly>
                                            </InputAddress2>
                                            {
                                                 formik.touched.address && formik.errors.address &&
                                                 <li><ErrMsg>{formik.errors.zipCode}</ErrMsg></li>
                                            }
                                            </AddressNumber>
                                            <AddressNumber>
                                            <InputInformation 
                                                name="addressDetail"
                                                placeholder="(  )"
                                                value={formik.values.addressDetail}
                                                onChange={formik.handleChange}
                                                onBlur={formik.handleBlur}
                                            ></InputInformation>
                                            </AddressNumber>
                                        </ul>
                                    </AddressArea>
                                </Tr>
                                <Tr>
                                    <CheckedItem></CheckedItem>
                                    <SelectContainer>
                                        <InputPhone 
                                            onBlur={formik.handleBlur}
                                            name='phone'
                                            placeholder="' - '     "
                                            value={formik.values.phone}
                                            onChange={formik.handleChange}
                                            maxLength={11}>
                                        </InputPhone>
                                        {
                                                 formik.touched.phone && formik.errors.phone &&
                                                 <li><ErrMsg>{formik.errors.phone}</ErrMsg></li>
                                            }
                                
                                    </SelectContainer>
                                </Tr>
                                <Tr>
                                    <Item></Item>
                                    <SelectContainer>
                                        <InputBasicPhone
                                         onBlur={formik.handleBlur}
                                         name='phone2'
                                         placeholder="' - '     "
                                         value={formik.values.phone2}
                                         onChange={formik.handleChange}
                                         maxLength={11}>
                                        </InputBasicPhone>
                                        {
                                                 formik.touched.phone2 && formik.errors.phone2 &&
                                                 <li><ErrMsg>{formik.errors.phone2}</ErrMsg></li>
                                            }
                                    </SelectContainer>
                                </Tr>
                                <Tr>
                                    <CheckedItem></CheckedItem>
                                    <td>
                                        <InputInformation
                                            onBlur={formik.handleBlur}
                                            name='email'
                                            value={formik.values.email}
                                            onChange={formik.handleChange}>
                                        </InputInformation>
                                           
                                             {
                                                formik.touched.email && formik.errors.email &&
                                                <ErrMsg>{formik.errors.email}</ErrMsg>
                                                }
                                    </td>
                                </Tr>
                            </tbody>
                        </table>
                    </BasicInformation>
                    <Title>
                        <h3><span>()</span></h3>
                        <p>    .</p>
                    </Title>
                    <BasicInformation>
                        <table>
                            <tbody>
                                <Tr>
                                    <Item></Item>
                                    <td>
                                        <InputBirth 
                                            onBlur={formik.handleBlur}
                                            type='text'
                                            name='birth'
                                            value={formik.values.birth}
                                            onChange={formik.handleChange}
                                            placeholder='ex)1988'
                                            maxLength={4}>
                                        </InputBirth>
                                        <TextInput></TextInput>
                                        <InputBirth 
                                            onBlur={formik.handleBlur}
                                            name='month'
                                            value={formik.values.month}
                                            onChange={formik.handleChange}
                                            placeholder="ex)03"
                                            maxLength={2}></InputBirth>
                                        <TextInput></TextInput>
                                        <InputBirth 
                                            onBlur={formik.handleBlur}
                                            name='date'
                                            value={formik.values.date}
                                            onChange={formik.handleChange}
                                            placeholder="ex)29"
                                            maxLength={2}></InputBirth>
                                        <TextInput></TextInput>
                                        {
                                            formik.touched.birth && formik.errors.birth &&
                                            <ErrMsg2>{formik.errors.birth}</ErrMsg2> || 
                                            formik.touched.month && formik.errors.month &&
                                            <ErrMsg2>{formik.errors.month}</ErrMsg2> ||
                                            formik.touched.date && formik.errors.date &&
                                            <ErrMsg2>{formik.errors.date}</ErrMsg2> 
                                        }
                                        <BirthSolar>
                                            <div>
                                            <span></span>
                                            <Radio
                                                checked={selectedValue === 'a'}
                                                onChange={handleChange}
                                                value="a"
                                                color="default"
                                                name="radio-buttons"
                                                inputProps={{ 'aria-label': 'A' }}
                                            ></Radio>
                                             <span></span>
                                            <Radio
                                                checked={selectedValue === 'b'}
                                                onChange={handleChange}
                                                value="b"
                                                color="default"
                                                name="radio-buttons"
                                                inputProps={{ 'aria-label': 'B' }}
                                            />
                                           
                                            </div>
                                        </BirthSolar>
                                    </td>
                                </Tr>
                                <Tr>
                                    <Item></Item>
                                    <SelectContainer>
                                        <select>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                            <option></option>
                                        </select>
                                    </SelectContainer>
                                </Tr>
                                <Tr>
                                    <Item> </Item>
                                    <td>
                                        <InputInformation />
                                    </td>
                                </Tr>
                            </tbody>
                        </table>
                    </BasicInformation>
                    <JoinBtnContainer>
                        <button></button>
                    </JoinBtnContainer>
                </form>
    
    </>
 )
};