import App from './App';
import {useNavigate} from "react-router-dom";
import {useState} from "react";
import {useFormik} from "formik";
import { object, string,ref } from 'yup';
import { And, AppContainer, Arrow, ErrMsgs, FormBox, FormInput, Input, InputBtn, JoinBtnWrapper, JoinMemberBtn, JoinText, KakaoLogin, LoginBar, LoginKakaoBtn, LoginLeft, LoginRight, LoginRightContainer, LoginSideBar, LoginWrapper, SearchBtn, SearchMembers, SnsBtn, SnsLogin, SnsTitle, Text, Text2, Top } from './styled/loginStyled';
import { CheckBtn, CloseBtn, ModalDiv, ModalWrap } from './modal/joinModal';
import CloseIcon from '@mui/icons-material/Close';

let loginSchema = object({
  id:string(' ')
  .min(4, '4  ')
  .max(10, '10  '),
  password:string(' ')
  .min(6, '6  ')
  .max(20, '20  ')
});

export const LoginPage = ()=>{
    
  //
  const [open, setIsOpen] = useState(false);

  // 
  const openModal = ()=>{
      setIsOpen(true)
      document.querySelector('body').style.overflow = 'hidden';
  }
  // 
  const closeModal = ()=>{
      setIsOpen(false)
      document.querySelector('body').style.overflow = 'auto';
  }

  const formik = useFormik({
    initialValues : {email : '', password:''},
    onSubmit : ()=>{
      openModal();
    },
    validationSchema : loginSchema
  })

    const navigate = useNavigate();
    const Success = ()=>{
      navigate('/')
  }
 

    return(
        <>
        <AppContainer>
        <Top></Top>
        <form onSubmit={formik.handleSubmit}
                      className='input_box'>
        <LoginWrapper>
          {/*  */}
            <LoginLeft>
              <LoginBar> </LoginBar>
              <Text> <br />
                   .
              </Text>
                <KakaoLogin>
                  <LoginKakaoBtn onClick={()=>navigate('/kakaologin')}></LoginKakaoBtn>
                </KakaoLogin>
            </LoginLeft>
            {/*  */}
            <LoginSideBar></LoginSideBar>
            {/*  */}
            <LoginRight>
              <LoginRightContainer>
                <LoginBar> 
                  <Text2> </Text2>
                </LoginBar>
              </LoginRightContainer>
              <FormBox>
                  <FormInput>
                    <Input 
                      onBlur={formik.handleBlur}
                      onChange={formik.handleChange}
                      value={formik.values.id}
                      id='id' name='id' placeholder=' '></Input>
                        {
                          formik.touched.id && formik.errors.id &&
                          <ErrMsgs>{formik.errors.id}</ErrMsgs>
                          }
                      {/* {touch.email &&<p>{errMsgs.email}</p>} */}
                  </FormInput>
                  <FormInput>
                    <Input
                      onBlur={formik.handleBlur}
                      onChange={formik.handleChange}
                      id='password' type="password" name='password' placeholder=' '></Input>
                        {
                          formik.touched.password && formik.errors.password &&
                          <ErrMsgs>{formik.errors.password}</ErrMsgs>
                          }
                      {/* {touch.password && <p>{errMsgs.password}</p>} */}
                  </FormInput>
                <div>
                  <InputBtn type="submit"></InputBtn>
                </div>
                <SearchMembers>
                  <SearchBtn> </SearchBtn>
                  <And>|</And>
                  <SearchBtn> </SearchBtn>  
                </SearchMembers>
                {/* sns login */}
                  <SnsTitle>
                    <SnsLogin>SNS </SnsLogin>
                  </SnsTitle>
                  <div>
                    <SnsBtn></SnsBtn>
                  </div>
                  <JoinBtnWrapper>
                    <JoinMemberBtn onClick={()=>navigate('/join_agreement')}>
                      <JoinText></JoinText>
                      <Arrow></Arrow>
                    </JoinMemberBtn>
                  </JoinBtnWrapper>
              </FormBox>
            </LoginRight>
           </LoginWrapper>
           <ModalWrap open={open}>
                <ModalDiv>
                    <p>{formik.values.id}  .</p>
                    <CloseBtn onClick={closeModal}> <CloseIcon/></CloseBtn>  
                    <CheckBtn onClick={Success}></CheckBtn>
                </ModalDiv>
            </ModalWrap>
         </form>
      </AppContainer>
    </>
    )
}